body {
  margin: 0;
  background-color: #e5e5e5;
}

.header {
  max-width: 1200px;
  height: 60px;
  display: flex;
  justify-content: space-between;
  background-color: #fff;
  margin: 0 auto;
}

.container-logo {
  display: flex;
  align-items: center;
  padding-left: 50px;
}

.link-logo {
  padding: 0;
  margin: 0;
  font-size: 0;
}

.logo {
  padding: 0;
  margin: 0;
}

.nav {
  display: flex;
  align-items: center;
  padding-right: 40px;
}

.nav-list {
  display: flex;
}

.is-active {
  text-decoration: none;
  padding: 0;
  margin: 0;
  font-size: 0;
}

.nav-item {
  text-transform: uppercase;
  list-style-type: none;
  font-size: 12px;
  color: #000;
  font-family: Roboto, sans-serif;
  padding: 18.5px 6px;
  border: 4px solid transparent;
}

.nav-item:hover {
  color: #00acdc;
  border-bottom: 4px solid;
  border-radius: 4px;
}
